
import { isScrollBottom } from "@/utils/comm.js";
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Badge from 'vux/src/components/badge/index.vue'
import XHeader from 'vux/src/components/x-header/index.vue'
import ConfirmPlugin from 'vux/src/plugins/confirm/index.js'
import XSwitch from 'vux/src/components/x-switch/index.vue'

import { mapGetters, mapActions, mapMutations } from "vuex";
import { api } from "@/utils/api.js";
export default {
components: {
XHeader
},
data(){
return {
currentTab:1,
listObj: [],
page: 0,
show_none:false,
show_bottom:false
}
},
created(){

},
destroyed () {

},
mounted(){
this.$nextTick(()=>{
var typeNum='';
typeNum=Number(typeNum);
if (typeNum){
this.currentTab=typeNum;
this.record_list(typeNum);
}else{
this.record_list(1);
}
isScrollBottom(this.scrollBottomCB);
});
},
methods: {

scrollBottomCB() {
var that=this;
var total_count = that.total_count;
var listObjLen = that.listObj.length;
var currentTab = that.currentTab;
currentTab = Number(currentTab);

if(currentTab){
if (total_count > listObjLen) {
this.record_list(currentTab);
} else {
this.show_bottom=true;
return false;
}
}
},
choiceTab(tab){
this.$vux.loading.show({
text: `正在加载数据`
});

this.currentTab=tab;
this.page=0;
this.listObj=[];
this.show_bottom=false;
tab=Number(tab);
this.record_list(tab);

},
async record_list(recordType){
var that=this;
var page = that.page;
page++;
let data = {
plat: 4,
token:this.token,
account:this.account,
page:page,
record_type: recordType
}
const [err, res_data] = await api.detailRecord(data);
if(err){
this.$vux.toast.text(err.msg);
return;
}
if(res_data.code == 2000){

var resList = res_data.data.list;
var total_count = res_data.data.total_count;
var listObj = that.listObj;
var page = that.page;
page++;

var newListObj = listObj.concat(resList);















if(total_count<=0){
that.show_none=true;
}else{
that.show_none=false;
}

that.listObj=newListObj;
that.page=page;
that.total_count=total_count;
this.$vux.loading.hide();


}
}
},
computed: {
...mapGetters(["user", "account", "token", "carpayInfo"])
},
}



